.DateNames {
    display: -ms-flexbox;
    display: flex;
    width: 1000px;
    margin: 0 auto;
    margin-top: 15px;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.date {
    font-family: sans-serif;
    font-size: 11px;
    font-weight: bold;
    letter-spacing: 4px;
    color: rgba(255, 255, 255, 0.9);
    width: 170px;
    text-align: center;
}



